
const FastClick = require('fastclick')
FastClick.attach(document.body);
import tools from '../lib/tools.js';
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name:'buyAction',
props:['price','goodId','type','userId','stock'],
components:{Toast,Group,Popup,Cell,XNumber,Spinner,XDialog},
directives: {TransferDom},
data(){
return {
id:'',
showAddress:false,
userInfo:{},
allAddress:'',
tempAddress:{},
showToast:false,
toastMsg:'',
isEdit:false,
numbers:1,
totalPrice:0,
showDialogPay:false,
sourceType:0,
sourceFromType:0,
sourceUserId:'',
sourceOrderId:''
}
},
mounted(){
if(tools.getRequestParam('buy') && sessionStorage.getItem('tempAddress')){
let address = JSON.parse(sessionStorage.getItem('tempAddress'));
this.tempAddress = address;
console.log('loading==',address);
this.allAddress = address.name +''+ address.mobile +''+ (address.address && address.address.split(' ')) +''+ address.detailedAddress +''+ address.zipCode
this.showAddress = true;
sessionStorage.removeItem('tempAddress');
}
if(sessionStorage.getItem('tempPrice') && sessionStorage.getItem('tempNumbers')){
this.price = sessionStorage.getItem('tempPrice');
this.numbers = sessionStorage.getItem('tempNumbers');
this.totalPrice = tools.mul(this.numbers,this.price);
sessionStorage.removeItem('tempPrice');
sessionStorage.removeItem('tempNumbers');
}

let params = tools.getRequestParam();
this.sourceType = params.sourceType || 1;
this.sourceUserId = params.sourceUserId || '';
this.sourceFromType = params.sourceFromType || '';
this.sourceOrderId = params.sourceOrderId || '';




},
methods:{

loadUser(){

var vm = this;
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || this.userId || tools.getCookie('user_id')) ; // md===userid
if(!id || id=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
}
this.id = id;
this.totalPrice = this.price;
this.$http.post('/apiUser/queryAccount?managerId='+id,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data){
vm.userInfo = data.data;
if(vm.userInfo.address && (/^\{.*\}$/.test(vm.userInfo.address))){
let tempAddressObj = JSON.parse(vm.userInfo.address);
console.log('buy==',tempAddressObj);
vm.allAddress = tempAddressObj.name +' '+ tempAddressObj.mobile +' '+ tempAddressObj.address + ' ' + tempAddressObj.detailedAddress;
}else{
this.isEdit = true;//    truefalse
vm.showToast = true;
vm.toastMsg = '';
}
this.showAddress = true;
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
setAddress(){
this.loadUser();
},
buyGood(){
/*  
sourceType : 0  

sourceType : 1  

sourceType : 2 , 
sourceFromType: 1 , 
sourceOrderId:  id
sourceFromType: 2 , 
sourceUserId: ID
sourceFromType: 3 , 
sourceUserId: ID
*/

if(this.isEdit || (!this.allAddress)){
this.showToast = true;
this.toastMsg = ',';
return false;
}
let uri = window.location.href.indexOf('serviceIndex.html')>=0?'/m/myService.html':'/m/myOrderList.html';

console.log(this.price,this.goodId,this.type,this.id);
this.id = (this.id || localStorage.getItem('md') || tools.getCookie('user_id'));
let vm = this;
if(this.numbers<1){
this.showToast = true;
this.toastMsg = '';
return false;
}
vm.showDialogPay = true;
let tempSourceStr = '';







tempSourceStr += `&sourceType=${vm.sourceType}`;
if(vm.sourceType==2){
tempSourceStr += `&sourceFromType=${vm.sourceFromType}`;
if(vm.sourceFromType && vm.sourceFromType == 1){
tempSourceStr += `&sourceOrderId=${vm.sourceOrderId}`;
}else if(vm.sourceFromType && (vm.sourceFromType == 2 || vm.sourceFromType == 3)){
tempSourceStr += `&sourceUserId=${vm.sourceUserId}`;
}
}
console.log("id="+vm.id+"&&goodsId="+vm.goodId+"&&type="+vm.type+"&buyCount="+vm.numbers+tempSourceStr);

this.$http.axios({method:'post',url:'/api/WeiXin/wx_project_pay.action?userId='+vm.id+'&goodsId='+vm.goodId+'&type='+vm.type+'&buyCount='+vm.numbers+tempSourceStr,data:{}}).then((result) => {
console.log("")
var data = result.data || result;

function onBridgeReady(){
WeixinJSBridge.invoke(
'getBrandWCPayRequest', {
"appId":data.appId,     //
"timeStamp":data.timeStamp,         //1970
"nonceStr":data.nonceStr, //
"package":"prepay_id="+data.prepayId,
"signType":"MD5",         //
"paySign":data.sign1 //
},
function(res){
if(res.err_msg == "get_brand_wcpay_request:ok" ) {
setTimeout(function(){
window.location.href=uri;
},2000);
}     // ,res.err_msg    ok
}
);
}
console.log(data);
if(data.ret_code=="0"){
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
}
}else{
onBridgeReady();
}
}else if(data.ret_code=="3"){
vm.showToast = true;
vm.toastMsg = data.ret_msg;
setTimeout(function(){
window.location.href=uri;
},1500);
}else{
vm.showDialogPay = false;
vm.showAddress = false;
vm.showToast = true;
vm.toastMsg = data.ret_msg;
return false;
}

},(error) => {
vm.showDialogPay = false;
vm.showAddress = false;
console.log("error");
vm.showToast = true;
vm.toastMsg = '';
console.error(error);
})
},
editAddress(){
let id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));

sessionStorage.setItem('tempURL',window.location.href + '&buy=buy');
sessionStorage.setItem('tempPrice',this.totalPrice);// 
sessionStorage.setItem('tempNumbers',this.numbers); // 
tools.next('/m/editAddress.html?md='+id);
},
numberFn(){
if(this.numbers<1){
this.showToast = true;
this.toastMsg = '';
return false;
}
this.totalPrice = tools.mul(this.numbers,this.price);
}
}
}



