// import toolbar from '../../components/Toolbar.vue';
import buyAction from '../../components/BuyAction.vue';
import tools from '../../lib/tools.js';
export default {
    name:'equipmentIndex',
    components:{buyAction},
    data(){
        return {
            info:{},
            isBuy:false,
            USERID:'',
            goodId:'',
            goodStock:''
        }
    },
    mounted(){
        this.loading();
        this.wxConfig();
    },
    methods:{
        loading(){
            if(tools.getRequestParam('isBuy')==1){this.isBuy = true;}
            var vm = this;
            let id = tools.getRequestParam('goodId');//mdid
            // if(!id){return false;}
            this.id = id;
            let intrlligentUrl = sessionStorage.getItem('intrlligentUrl');
            let url = '/goodsInfo/getGoodsInfoById?id='+id;
            if(intrlligentUrl){
                url = intrlligentUrl;
                sessionStorage.removeItem('intrlligentUrl');
            }
            this.$http.post(url,{}).then((result) => {
                let data = result.data;
                if(data.successful && (data.status==200)){
                    if(data.data){
                        vm.info = data.data;
                        vm.USERID = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id')); // md===userid
                        document.getElementById('contentText').innerHTML = vm.info.goodsDesc;
                        vm.goodStock = vm.info.stock;
                        // img
                        const imgs = document.querySelectorAll('#contentText img')
                        for(var i=0;i<imgs.length;i++){
                            imgs[i].className = 'img-responsive';
                            imgs[i].style.display = 'block';
                            imgs[i].style.maxWidth = '100%';
                            imgs[i].style.height = 'auto';
                        }
                        sessionStorage.removeItem('tempURL');
                    }
                }else{
                    vm.showToast = true;
                    vm.toastMsg = '';
                    console.log(data);
                }
            },(error) => {
                console.log(error);
                vm.showToast = true;
                vm.toastMsg = '';
            });
        },
        wxConfig(){
            let _this = this;
            let config = {
                jsList:['onMenuShareTimeline','onMenuShareAppMessage'],
                url:location.href.split('#')[0]
            }
            let url = '';
            let id = localStorage.getItem('md') || tools.getCookie('user_id');//
            url = id?'&sourceUserId='+id:'';
            tools.wxConfig(this.$http,config,(result) => {
                wx.ready(function() {
                    let shareData = {
                        title: _this.info.goodsName,
                        desc: _this.info.goodsName,
                        link: result.link+ 'm/sharing.html' + '?type=4&id='+_this.id+url,
                        imgUrl:  result.link+'m/static/img/20170817165354.jpg',
                    };
                    console.log('shareData==',shareData);
                    wx.onMenuShareAppMessage(shareData);
                    wx.onMenuShareTimeline(shareData);
                });
            });
        }
    }
}



